import $ from "jquery"
import is from "is_js"
import Toast from "./toast"
window.jQuery = $
window.$ = $
window.is = is

import Inputmask from "inputmask"

import { Swiper, Navigation, Thumbs, EffectFade, Autoplay, Lazy, Pagination } from "swiper/swiper.esm"

Swiper.use([Navigation, Thumbs, EffectFade, Autoplay, Lazy, Pagination])

require("./jquery.fancybox.js")

document.addEventListener("DOMContentLoaded", function () {
    // всплывающие уведомления
    window.toast = new Toast()

    let im = new Inputmask("+7 (999) 999-99-99")
    im.mask(document.querySelectorAll('input[type="tel"]'))

    if (!window.App) commonInit()

    $("body").on("click", ".more-text", function() {
        const btn = this;
        // console.log(btn);
        // moreFlag = true;
        btn.closest(".title-block__text").querySelector(".tb-text").classList.toggle("js__fade")

        if (btn.innerHTML == "Подробнее") {
            btn.innerHTML = "Показать меньше"
            document.querySelector(`.tb-text`).style.height = "auto"
        } else {
            btn.innerHTML = "Подробнее"
            document.querySelector(`.tb-text`).style.height = 120 + "px"
        }
    });

    // EXPAND
    let expandButtons = document.querySelectorAll(".expand-button")
    if (expandButtons.length) {
        expandButtons.forEach((button) => {
            button.addEventListener("click", (e) => {
                e.target.classList.toggle("expanded")
                e.target.previousElementSibling.classList.toggle("expanded")
            })
        })
    }

    if (document.querySelector(".head-fixed__menu")) {
        document.querySelector(".head-fixed__menu").addEventListener("click", function (e) {
            // console.log(this);
            e.stopPropagation()
            document.body.classList.add("js__menu-open")
        })

        if (document.querySelector(".mobile-menu__top"))
            document.querySelector(".mobile-menu__top").addEventListener("click", function (e) {
                // console.log(this);
                e.stopPropagation()
                document.body.classList.remove("js__menu-open")
            })

        if (document.querySelector(".main-header .burger"))
            document.querySelector(".main-header .burger").addEventListener("click", function (e) {
                // console.log(this);
                e.stopPropagation()
                document.body.classList.add("js__menu-open")
            })
    }

    $(document).click(function (e) {
        if (!$(".mobile-menu-cont").is(e.target) && !$(e.target).is($(".mobile-menu-cont")) && !$(".main-header .burger").is(e.target) && !$(e.target).is($(".main-header .burger")) && !$(".head-fixed__menu").is(e.target) && !$(e.target).is($(".head-fixed__menu"))) {
            $("body").removeClass("js__menu-open")
        }
    })

    $('.footer__menu .main-nav__item--hassubmenu').each(function(){
        $(this).click(function(){
            $(this).siblings('.main-nav__item--hassubmenu').removeClass('open');
            $(this).toggleClass('open')
        });
    });

})

const fancyboxInit = () => {
    $(".fancybox").fancybox({
        touch: false,
        trapFocus: false,
        buttons: ["fullscreen", "close", "thumbs"],
        image: {
            preload: true,
        },
        transitionEffect: "slide",
    })
}

window.setBookingProgress = (num) => {
    let val = parseInt(num);
    let $circle = $('#svgCircle #circleBar');
    let $line = $('#progCount');

    if (isNaN(val)) {
        val = 100;
    } else {
        let r = $circle.attr('r');
        let c = Math.PI*(r*2);
        if (val < 0) { val = 0;}
        if (val > 100) { val = 100;}

        let pct = ((100-val)/100)*c;
        $circle.css({ strokeDashoffset: pct});
        $line.css({ width: num + '%'})
        $('#progCircle').attr('data-pct',val);
        $('#progCountValue').attr('data-pct',val);
    }
}

