window.checkServicesEdit = () => {
    if (document.querySelector(".edit")) {
        document.querySelectorAll(".edit").forEach((btn) =>
            btn.addEventListener("click", function () {
                let input = btn.previousElementSibling

                if (input.getAttribute("readonly")) {
                    input.removeAttribute("readonly")
                    input.classList.add("active")
                } else {
                    input.setAttribute("readonly", "readonly")
                    input.classList.remove("active")
                }
            }),
        )
    }
}

window.commonInit = () => {
    fancyboxInit()

    window.tabInit()

    const topSlider = document.querySelector(".top-block__slider .swiper-container")
    var topSwiper = new Swiper(topSlider, {
        effect: "fade",
        fadeEffect: {
            crossFade: true,
        },
        loop: true,
        autoplay: true,
        lazy: {
            loadPrevNext: true,
        },
    })

    new Swiper(".services-block .swiper-list", {
        spaceBetween: 0,
        slidesPerView: 1,
        navigation: {
            nextEl: ".services-block .services__arrow-el--next",
            prevEl: ".services-block .services__arrow-el--prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            661: {
                slidesPerView: 3,
            },
        },
    })

    let windowHeight = document.documentElement.clientHeight

    $(window).on("scroll", function () {
        let top = $(this).scrollTop()
        if (top > windowHeight + 250) {
            $(".head-fixed").addClass("js__visible")
        } else {
            $(".head-fixed").removeClass("js__visible")
        }
    })

    if (document.querySelector(".ac-more")) {
        document.querySelectorAll(".ac-more").forEach((btn) =>
            btn.addEventListener("click", () => {
                btn.closest(".arenda__item").classList.toggle("js__open")
            }),
        )
    }

    $("body").on("mousedown", "select:not(.no-my-select)", function (e) {
        const $this = $(this)

        if (is.touchDevice()) return

        e.preventDefault()

        if (this.classList.contains("my-select")) $this.closest("div").find(".my-select__list-cont").remove()

        const select = window.selectInitial(this)

        this.classList.add("my-select")
        this.closest("div").style.position = "relative"

        $this.addClass("js__opened")
    });

    const positionTitle = () => {
        if (document.querySelector(".standard-page-cont") && window.matchMedia("(min-width: 660px").matches) {
            document.querySelector(".standard-page-cont").closest("body").querySelector(".title-block__title").style.justifyContent = "center"
        }
    }
    positionTitle()

    const tabContHiddenText = () => {
        setTimeout(function () {
            if (document.querySelector(`.tb-text`)) {
                let heightCont = document.querySelector(`.tb-text`).getBoundingClientRect(),
                    textToggle = document.querySelector(`.more-text`)

                if (heightCont.height >= 120) {
                    document.querySelector(`.tb-text`).style.height = 120 + "px"
                    document.querySelector(`.tb-text`).classList.add("js__fade")
                } else {
                    if (textToggle) {
                        document.querySelector(".more-text").remove()
                    }
                }
            }
        }, 0)
    }

    window.addEventListener("load", tabContHiddenText)

    // checks
    $(".checks-card").each(function () {
        $(this)
            .find(".checks-card__status")
            .on("click", function () {
                $(this).siblings(".checks-card__message").addClass("js__open")
            })

        $(this)
            .find(".close")
            .on("click", function () {
                $(this).parent(".checks-card__message").removeClass("js__open")
            })
    })

    checkServicesEdit()

}

